import React from 'react';
import Game from './game';
import processSchedule from '../lib/data';

class Schedule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      isError: false,
      schedule: {}
    };
  }

  componentDidMount() {
    fetch('https://api.lolesports.com/api/v1/scheduleItems?leagueId=2')
      .then(res => res.json())
      .then(
        (result) => {
          console.log("JSON response: " + JSON.stringify(result, null, 4));
          let schedule = processSchedule(result);
          console.log("schedule: " + JSON.stringify(schedule, null, 4));
          this.setState({
            isLoaded: true,
            schedule: schedule
          });
        },
        (error) => {
          this.setState({
            isError: true
          });
        }
      );
  }

  render() {
    const { isLoaded, isError, schedule } = this.state;

    if (!isLoaded) {
      return (
        <div>Loading..</div>
      );
    }

    if (isError) {
      return (
        <div>Oops! An error occurred.</div>
      );
    }

    return (
      <div>
        <table>
          <tbody>
            {
              [...schedule].sort((left, right) => {
                return right.scheduleEpoch - left.scheduleEpoch;
              }).reverse().map(item => (
                <Game key={item.id} teams={item.teams} date={item.scheduledTime} isPast={item.isPast} />
              ))
            }
          </tbody>
        </table>
      </div>
    );
  }
}

export default Schedule



