import React from 'react';
import moment from 'moment';
import _ from 'lodash';
import './game.css'

const Game = ({teams, date, isPast, id}) => {
  const logoSize = 90;

  // This is a dirty band-aid for some misunderstanding
  // of mine with how react lifecycle works. My understanding was that if this Game
  // is rendered only if the data is available, but apparently that's not the case
  // with the way I'm using componentDidMount in <Schedule>.
  if (_.isEmpty(teams)) {
    return null;
  }

  return (
    <tr className={isPast ? 'game past-game' : 'game'}>
      <td>
        {moment(date).format('ddd MMM DD YY, hh:mm:ss a ')}
      </td>
      <td>
        {teams[0].name}
      </td>
      <td>
        <img className='team-logo' src={"https://am-a.akamaihd.net/image/?f=" + teams[0].logoUrl + "&resize=" + logoSize + ":" + logoSize}/>
      </td>
      <td>
        <span className='vs'>vs</span>
      </td>
      <td>
        {teams[1].name}
      </td>
      <td>
        <img className='team-logo' src={"https://am-a.akamaihd.net/image/?f=" + teams[1].logoUrl + "&resize=" + logoSize + ":" + logoSize}/>
      </td>
    </tr>
  );
};

export default Game;



