import _ from 'lodash';
import moment from 'moment';

// example scheduleitem: https://gist.github.com/zdwolfe/670651c98ec2aeb346b9e7cabc397fe6

function logThe(thing) {
}

function shouldDisplayView(view) {
  var scheduledTime = _.get(view, 'scheduledTime');
  var scheduledTimeMoment = moment(scheduledTime);
  var isBeforeNextWeek = scheduledTimeMoment.isBefore(moment().add(7, 'days'));
  var isAfterLastWeek = scheduledTimeMoment.isAfter(moment().subtract(7, 'days'));
  return isBeforeNextWeek && isAfterLastWeek;
}

function mapScheduleItemsToMatchIds(scheduleItem) {
  return _.get(scheduleItem, 'match');
}

function getMatchViews(schedule) {
  var tournaments = _.get(schedule, 'highlanderTournaments');
  var teams = _.get(schedule, 'teams');
  var teamsByTeamId = _.keyBy(teams, 'id');

  var matches = _.flatMap(tournaments, function(tournament) {
    var tournamentRosters = _.get(tournament, 'rosters');
    var tournamentId = _.get(tournament, 'id');
    var tournamentDesc = _.get(tournament, 'description');
    return _.flatMap(_.get(tournament, 'brackets'), function(bracket) {
      var bracketId = _.get(bracket, 'id');
      return _.flatMap(_.get(bracket, 'matches'), function(theMatch) {

        var matchTeams = _.map(_.get(theMatch, 'input'), function(input) {
          var roster = _.get(tournamentRosters, _.get(input, 'roster'));//, 'name')
          var teamId = _.get(roster, 'team');
          var team = _.get(teamsByTeamId, teamId);
          return {
            teamId: teamId,
            acronym: _.get(team, 'acronym'),
            name: _.get(team, 'name'),
            logoUrl: _.get(team, 'logoUrl'),
            rosterId:  _.get(roster, 'id'),
            startingPlayerIds: _.get(team, 'starters')
          };
        });

        return {
          matchId: _.get(theMatch, 'id'),
          bracketId: bracketId,
          tournamentId: tournamentId,
          tournamentDescription: tournamentDesc,
          teams: matchTeams
        };
      });
    });
  });

  return matches;
}

function getMatchScheduleViews(matchViews, scheduleItems) {
  // assumes a 1-1 map of scheduleItem to match
  var scheduleItemViews = _.map(scheduleItems, function(scheduleItem) {
    return _.assign({
      scheduleItemId: _.get(scheduleItem, 'id'),
      scheduleEpoch: moment(_.get(scheduleItem, 'scheduledTime')).unix(),
      isPast: moment(_.get(scheduleItem, 'scheduledTime')).isBefore(moment())
    }, scheduleItem);
  });
  var scheduleItemsByMatchId = _.keyBy(scheduleItemViews, 'match');
  var matchesByMatchId = _.keyBy(matchViews, 'matchId');
  return _.merge(scheduleItemsByMatchId, matchesByMatchId);
}

function processSchedule(schedule) {
  var scheduleItems = _.get(schedule, 'scheduleItems');

  var matchViews = getMatchViews(schedule);
  var matchScheduleView = getMatchScheduleViews(matchViews, scheduleItems);
  var matchScheduleViews = _.values(matchScheduleView);
  var thisWeeksMatchScheduleViews = _.filter(matchScheduleViews, shouldDisplayView);

  return thisWeeksMatchScheduleViews;
}

export default processSchedule;


